	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/again-about-finland/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Продолжаю рассказывать про Финляндию, всего по чуть-чуть. Если хотите освежить память, вот <a href=\"http://varya-daily.tumblr.com/post/110379049793/1\">ссылка на прошлый рассказ</a>.</p>\n<h2>Как общаться?</h2>\n<p>В прошлый раз спрашивали про язык. Отвечаю (и по опыту Голландии и Финляндии) — английский знают все. Для решения бытовых вопросов локальный язык знать не обязательно. Для работы — тем более. Я думаю, это справедливо для всех маленьких стран.\n<excerpt/></p>\n<p>В жизни всё делается на английском. То есть квартиру снять, на почте товар получить, оформить страховку, с врачом пообщаться, в магазине спросить что-то у продавца — это всё на английском. Я вообще не припомню случая, чтобы кто-то английский не знал. Один только раз тётенька, которая продавала страховки, говорила, что плохо знает английский. Но поскольку финского мы вообще не знали, так и общались на “плохом” английском. Пишу в кавычках, потому что по-моему там всё хорошо было.</p>\n<p>Конечно, хорошо бы понимать язык на слух без видеоряда — иногда придётся общаться по телефону. Многие вещи можно сделать онлайн, но иногда телефона не избежать. Просто для примера расскажу, что когда мы купили онлайн стиральную машинку, я неправильно написала свой номер телефона. Поэтому курьер не мог дозвониться, чтобы условиться о времени доставки. Пришлось звонить на почту, чтобы телефон исправить.</p>\n<p>По сравнению с другими маленькими странами в Финляндии есть своя особенность — много русскоязычных. Соцслужба и другие организации могут даже подобрать вам русскоязычного сотрудника для общения, если есть такая необходимость.</p>\n<h2>Локальный язык</h2>\n<p>Тем не менее локальный язык всё равно большой плюс, и мне кажется, его надо начинать учить ещё до въезда в страну. Много не выучишь, но хотя бы навигироваться по сайтам удобно.</p>\n<p>Поначалу нужно много гуглить, где какие организации и где что продаётся. Для этого всего тоже нужны локальные слова. Есть разные способы их узнавать (словать не всегда помогает).</p>\n<p>Для мебели и товаров для дома я сначала находила товар в русской ИКЕЕ. Потом точно такой же в финской. Так узнавала названия вещей, которые мне нужны.</p>\n<p>Для других вещей смотрела сайты объявлений. Там вывешивают фотографии и можно понять, что как называется.</p>\n<p>Плюс, конечно, у коллег можно всё спрашивать.</p>\n<h2>Неработающий партнер</h2>\n<p>В Финляндии есть программа адаптации имигрантов. По факту она применима только к партнёрам трудовых мигрантов, потому что её цель — устроить человека на работу. А поскольку трудовой мигрант уже устроен, считается, что ему это не надо.</p>\n<p>По приезду в Финляндию супругу или супруге мигранта стоит встать на учёт в службу занятости. О том, как это сделать, подробно рассказывают, когда в первый раз регистрируешься в стране. Вставать на учет в службу занятости не обязательно, но разумно вставать.</p>\n<p>В первый раз в службу занятости обращаются без записи. Там назначают дату языкового тестирования и интеграционного собеседования. Это может быть через какое-то время, в зависимости от очереди. Но момент первого обращения в службу занятости фиксируется сразу. Дальнейшее общение с чиновниками происходит по email, приходить никуда не надо. Письма пишут на русском.</p>\n<p>С этой даты (кроме первых двух недель “карантина”) безработному положено пособие. Получать его можно с того момента как будет готова социальная карта Kela. Она тоже делается какое-то время, до пары месяцев. Но деньги не пропадут, сразу после получения карты перечислят всё, что накопилось.</p>\n<p>Пособие по безработице около 600 евро в месяц.</p>\n<p>Безработный мигрант должен учить язык, потому что считается, что незнание языка и является препятствием для того, чтобы найти работу. Служба занятости может отправить на бесплатные курсы. А можно пойти на любые платные, если они достаточно интенсивны (несколько часов в день). Для обучающихся языку пособие увеличивается на 8-10 евро в рабочий день. В итоге вместе с основной суммой получается 800 евро в месяц.</p>\n<p>Бесплатных курсов приходится ждать несколько месяцев после интеграционного собеседования. И они могут оказаться плохими — это зависит от того, какая подберется группа. Но может и повезти.</p>\n<p>Саша сначала ходил на платные интенсивные курсы в университете, а сейчас ходит на бесплатные, ему попались хорошие — группа мотивированная, все учат и делают домашку. Ездить туда нужно каждый день, занятия с 9 до 14, плюс домашнее задание.</p>\n<p>Иногда вместо языка они изучают культурные особенности на работе, проводят профориентацию. Один раз было занятие с компьютерами. Надо было в компьютере с финской локализацией запускать Word и написать в нём своё резюме. Всё это тоже на финском, хотя, конечно, преподаватели стараются говорить медленно и объяснять всё простыми словами с жестами.</p>\n<h2>Общение с государством</h2>\n<p>Время от времени приходится общаться с государством. Например, выпустить местную карточку ID, поменять водительские права. В Финляндии всё это делается через полицию. Записываешься через Интернет на определенное время, там тебя подзывают к окошку и всё делают. Результат обычно присылают по почте. О том, какие документы для каких процедур нужны, можно узнать на сайте полиции — там всё подробно написано, есть версия на английском.</p>\n<p>Правда, полиция загружена. Записаться можно только на месяц вперед. Если очень срочно надо, то можно и без записи. Приходишь, берешь отрывной талончик и ждёшь своей очереди. Но длина у такой очереди может быть непредсказуемая, по записи удобнее.</p>\n<p>Сделать запись в полицию можно на несколько человек, там это называется “family and friends”. Ведь если будут разные записи, то могут позвать в разные окошки. А вдруг вы не хотите разлучаться? Вот для таких случаев есть совместная запись и время на приём предусмотрено дольше.</p>\n<h2>Поездки в Россию</h2>\n<p>Ездить в Питер из Хельсинки несравнимо удобнее, чем их других мест в Европе. Поезд Аллегро идёт 3.5 часа, и это время от центра города до центра города, без дороги в аэропорт. Плюс нет никаких досмотров. На обратном пути есть, но это другая история :-)</p>\n<p>В Петрозаводск ездим на автобусе. Занимает это 12 часов, стоит около 30 евро в 1 сторону. Автобус удобный — с чаем, кофе, продуманными остановками на заправках на туалет и перекусы. Внутри тоже туалет есть, но на заправках удобнее. Ехать 12 часов не особо приятно. Есть как ночные, так и дневные маршруты. Но ночью хорошо не поспишь, а днём жалко, что время пропадает. Но всё равно это намного лучше, чем 20-часовые путешествия из Амстердама. Там ни о каком “съезжу к маме на праздники” речи идти не могло.</p>\n<p>Назад из Петрозаводска обычно ездим на поезде с пересадкой в Питере.</p>\n<p>Надеюсь, что скоро купим машину, тогда поездки в Петрозаводск будут удобнее: 10-11 часов — это, конечно, тоже долго. Но можно выехать пораньше, часа в 3 дня. тогда в начале ночи будешь уже на месте.</p>\n<h2>Общественный транспорт</h2>\n<p>В прошлый раз немного писала про общественный транспорт, но напишу ещё. Метро в нашем районе нет, обещают через 5 лет, и уже начали долбить скалы. В центр ездим на автобусе. В принципе, большой разницы с метро нет, потому что автобус едет по выделенной полосе, то есть не стоит в пробках.</p>\n<p>Работа у меня в разных офисах. В основной офис автобус приезжает прямо в подвал торгового центра. И наша фирма там же.</p>\n<p>А если надо куда-то дальше ехать, то тут можно пересаживаться на метро, электрички или трамваи.</p>\n<p>Всё это по одному проездному. Это такая магнитная карточка, её можно “заряжать” проездными на 15-30 дней или деньгами. Деньги можно тратить на единичные поездки в далеких районах, где проездной не действует.</p>\n<p>Пополняют проездной в автоматах, там очень удобный интерфейс и исключено общение с кассиром :-) Но я пополняю в магазинчиках R-kioski. Там можно расплачиваться не деньгами, а виртуальным счетом, привязанным к телефону. Нам на работе завели такие счета, и мы покупаем проездной на них (в смысле — свои деньги не платим).</p>\n<p>Напишите мне, про что вам ещё рассказать. У меня есть темы, но ваши важнее :-)</p>","fields":{"slug":"ru/life/again-about-finland/","prefix":"","disqusIdentifier":"life-again-about-finland-index-ru"},"frontmatter":{"title":"Опять немного про Финлядию","date":"25 February 2015","v2":true,"old":null,"tumblr":true,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/again-about-finland/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/buying-car-finland/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/buying-car-finland/","prefix":"","lang":"ru","disqusIdentifier":"life-buying-car-finland-index-ru"},"frontmatter":{"title":"Как покупают машины в Финляндии"}},"next":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/bad-holland/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/bad-holland/","prefix":"","lang":"ru","disqusIdentifier":"life-bad-holland-index-ru"},"frontmatter":{"title":"Плохая Голландия"}}}}

